'use client'

import Link from 'next/link'
import { useState, useEffect } from 'react'
import { usePathname } from 'next/navigation'
import ThemeToggle from './ThemeToggle'

export default function Navigation() {
  const [isOpen, setIsOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)
  const [activeSection, setActiveSection] = useState('')
  const pathname = usePathname()

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)

      // Update active section based on scroll position
      const sections = ['hero', 'skills', 'projects', 'contact']
      const currentSection = sections.find(section => {
        const element = document.getElementById(section)
        if (element) {
          const rect = element.getBoundingClientRect()
          return rect.top <= 100 && rect.bottom >= 100
        }
        return false
      })
      setActiveSection(currentSection || '')
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const navItems = [
    { href: '/', label: 'Home', section: 'hero' },
    { href: '/about', label: 'About', section: 'about' },
    { href: '/projects', label: 'Projects', section: 'projects' },
    { href: '/contact', label: 'Contact', section: 'contact' }
  ]

  return (
    <nav className={`fixed top-0 w-full z-50 transition-all duration-500 ${
      isScrolled
        ? 'bg-gray-900/95 backdrop-blur-xl shadow-2xl border-b border-white/10'
        : 'bg-gray-900/80 backdrop-blur-lg'
    }`}>
      <div className="absolute inset-0 bg-gradient-to-r from-red-500/5 via-transparent to-blue-500/5"></div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
        <div className="flex justify-between h-20">
          {/* Logo */}
          <div className="flex items-center">
            <Link
              href="/"
              className="group relative text-3xl font-bold transition-all duration-500 hover:scale-105"
            >
              <span className="bg-gradient-to-r from-red-400 via-red-300 to-red-500 bg-clip-text text-transparent group-hover:from-red-300 group-hover:via-red-200 group-hover:to-red-400 transition-all duration-500">
                Hussein
              </span>
              <div className="absolute -bottom-1 left-0 w-0 h-0.5 bg-gradient-to-r from-red-400 to-red-500 group-hover:w-full transition-all duration-500"></div>
            </Link>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden lg:flex items-center space-x-1">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className={`relative px-6 py-3 rounded-2xl font-medium transition-all duration-500 group overflow-hidden ${
                  pathname === item.href || activeSection === item.section
                    ? 'text-white'
                    : 'text-gray-300 hover:text-white'
                }`}
              >
                {/* Background Effect */}
                <div className={`absolute inset-0 bg-gradient-to-r from-red-500/20 to-red-600/20 rounded-2xl transition-all duration-500 ${
                  pathname === item.href || activeSection === item.section
                    ? 'opacity-100 scale-100'
                    : 'opacity-0 scale-95 group-hover:opacity-100 group-hover:scale-100'
                }`}></div>

                {/* Active Indicator */}
                <div className={`absolute bottom-0 left-1/2 transform -translate-x-1/2 w-0 h-0.5 bg-gradient-to-r from-red-400 to-red-500 transition-all duration-500 ${
                  pathname === item.href || activeSection === item.section
                    ? 'w-8'
                    : 'group-hover:w-4'
                }`}></div>

                <span className="relative z-10">{item.label}</span>

                {/* Hover Glow */}
                <div className="absolute inset-0 bg-gradient-to-r from-red-500/10 to-blue-500/10 rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-500 blur-xl"></div>
              </Link>
            ))}

            {/* CTA Button */}
            <div className="ml-6">
              <a
                href="/contact"
                className="group relative overflow-hidden bg-gradient-to-r from-red-500 to-red-600 text-white px-6 py-3 rounded-2xl font-semibold transition-all duration-500 hover:from-red-600 hover:to-red-700 hover:scale-105 shadow-lg hover:shadow-red-500/25"
              >
                <span className="relative z-10">Let&apos;s Talk</span>
                <div className="absolute inset-0 bg-gradient-to-r from-red-400 to-red-500 opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
              </a>
            </div>

            <div className="ml-4">
              <ThemeToggle />
            </div>
          </div>

          {/* Mobile menu button */}
          <div className="lg:hidden flex items-center space-x-3">
            <ThemeToggle />
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="group relative p-3 bg-white/5 backdrop-blur-sm rounded-2xl hover:bg-white/10 transition-all duration-300 border border-white/10 hover:border-white/20"
              aria-label="Toggle menu"
            >
              <div className="w-6 h-6 flex flex-col justify-center items-center">
                <span className={`block w-5 h-0.5 bg-gray-300 transition-all duration-300 ${
                  isOpen ? 'rotate-45 translate-y-1' : '-translate-y-1'
                }`}></span>
                <span className={`block w-5 h-0.5 bg-gray-300 transition-all duration-300 ${
                  isOpen ? 'opacity-0' : 'opacity-100'
                }`}></span>
                <span className={`block w-5 h-0.5 bg-gray-300 transition-all duration-300 ${
                  isOpen ? '-rotate-45 -translate-y-1' : 'translate-y-1'
                }`}></span>
              </div>
            </button>
          </div>
        </div>

        {/* Mobile Navigation */}
        <div className={`lg:hidden transition-all duration-500 overflow-hidden ${
          isOpen ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'
        }`}>
          <div className="px-4 pt-4 pb-6 bg-white/5 backdrop-blur-lg rounded-3xl mt-4 shadow-2xl border border-white/10">
            <div className="space-y-2">
              {navItems.map((item, index) => (
                <Link
                  key={item.href}
                  href={item.href}
                  className={`group block px-6 py-4 rounded-2xl transition-all duration-300 font-medium ${
                    pathname === item.href || activeSection === item.section
                      ? 'text-white bg-red-500/20 border border-red-500/30'
                      : 'text-gray-300 hover:text-white hover:bg-white/5 border border-transparent hover:border-white/10'
                  }`}
                  onClick={() => setIsOpen(false)}
                  style={{ animationDelay: `${index * 50}ms` }}
                >
                  <div className="flex items-center justify-between">
                    <span>{item.label}</span>
                    <svg className={`w-4 h-4 transition-transform duration-300 ${
                      pathname === item.href || activeSection === item.section
                        ? 'translate-x-1'
                        : 'group-hover:translate-x-1'
                    }`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </div>
                </Link>
              ))}

              {/* Mobile CTA */}
              <div className="pt-4 border-t border-white/10">
                <a
                  href="/contact"
                  className="group block w-full bg-gradient-to-r from-red-500 to-red-600 text-white text-center py-4 px-6 rounded-2xl font-semibold transition-all duration-300 hover:from-red-600 hover:to-red-700 hover:scale-105 shadow-lg"
                  onClick={() => setIsOpen(false)}
                >
                  <span className="flex items-center justify-center gap-2">
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                    </svg>
                    Let&apos;s Talk
                  </span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Scroll Progress Indicator */}
      <div className="absolute bottom-0 left-0 w-full h-0.5 bg-gray-700">
        <div
          className="h-full bg-gradient-to-r from-red-500 to-red-600 transition-all duration-300"
          style={{
            width: `${Math.min((window.scrollY / (document.documentElement.scrollHeight - window.innerHeight)) * 100, 100)}%`
          }}
        ></div>
      </div>
    </nav>
  )
}
